              this.$refs[formName].resetFields()
              this[formName].mobile = ''
              this.region = '北京'
              this.xueli = '自考本科'
              this.zhuanye = '法考'
              this.worktime = '应届毕业生'
              this.dialogVisible=false
              this.isDisable = true
              setTimeout(() => {
                this.isDisable = false
              }, 2000)

            } else {
              this.$message.error(res.msg);
            }

          } else {
            console.log('error submit!!');
            return false;
          }
