  }

  .nine_con img {
    width: .24rem;
    height: .24rem;
    margin-right: .1rem;
    margin-top: .08rem;

  }

  .nine_con li {
    display: inline-block;
    width: 3.3rem;
    margin-top: .2rem;
    box-sizing: border-box;
    text-align: left;
  }

  .nine_con li span {
    display: inline-block;
    width: 2.5rem;
  }

  .nine_con {
    font-size: .24rem;
    font-family: PingFang;
    font-weight: 500;
    color: rgba(51, 51, 51, 1);
    // line-height:.3rem;
  }

  // 底部
  .foot {
    background: #fff;
    padding: .3rem .3rem .1rem;

    .firstul {
      margin: .2rem 0;

      li {
        font-size: 0.26rem;
        background: #3669e5;
        padding: 0.1rem;
        color: #fff;
        border-radius: 5px;
      }
    }

    .lastul {
      // margin: 10px 0px;
      // padding: 0px 10px;
      box-sizing: border-box;

      li {
        border-radius: 5px;
        margin-bottom: .2rem;

        .left {
          float: left;
          padding: 0 .1rem;
          box-sizing: border-box;
          width: 25%;
          padding-top: .8rem;

          p {
            color: #fff;
            font-size: .26rem;
          }

          p:nth-child(2) {
            color: #986c2c;
          }
        }

        .right {
          width: 75%;
          float: left;

          .title {
            padding: 8px 0px;
            color: #fff;
            font-size: .3rem;

          }

          .btn {
            padding: 5px 7px;
            background: #feac3c;
            color: #fff;
            width: 3rem;
            // margin-left: 1rem;
            border-radius: 10px;
            margin: 0.2rem 0rem 0rem 1rem;
          }

          div:nth-child(1) {
            color: #a9dabd;
            // background: #e3f1e9 ;
          }


        }
      }

      li:nth-child(1) {
        background: #92d2ad;
      }

      li:nth-child(2) {
        background: #f5a4a8;
      }

      li:nth-child(3) {
        background: #3669e5;
      }

      li:nth-child(1) .footcontent {
        color: #a9dabd;
        background: #e3f1e9;
      }

      li:nth-child(2) .footcontent {
        color: #f5a4a8;
        background: #f9ebec;
      }

      li:nth-child(3) .footcontent {
        color: #3669e5;
        background: #e2e4e9;
      }
    }
  }

  .footcontent {
    padding: .1rem 0;
    font-size: 0.24rem;
  }

  .book {
    background: #eee;
    // margin: 10px 0px;
    padding: 0 .3rem;
    box-sizing: border-box;

    .content {
      font-size: 0.26rem;
      color: #333;
      text-align: justify;
      margin: .2rem 0;

      span {
        color: #fd3a3a;
      }
    }

    .ul1 {
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-wrap: wrap;

      li {
        background: #d6e2fe;
        border-radius: 10px;
        width: 30%;
        margin-bottom: 10px;
        // margin-right: 11px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;

        .num {
          font-size: 0.3rem;
          color: #fff;
          background: #3669e5;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          padding: 5px 0px;
        }

        .title {
          font-size: 0.26rem;
          color: #3669e5;
          margin: 10px 0px;
        }

        .btn {
          background: #feac3c;
          color: #fff;
          font-size: 0.26rem;
          width: 1.5rem;
          margin: 20px auto;
          border-radius: 5px;
        }
      }

      li:last-child {
        visibility: hidden;
      }
    }

    .ul2 {
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-wrap: wrap;

      li {
        width: 40%;
        color: #fff;
        padding: 5px 0px;
        border-radius: 10px;

        p {
          font-size: 0.3rem;
        }
      }

      li:first-child {
        background: #fea73d;
      }

      li:last-child {
        background: #14d1ec;
      }
    }

    .ul3 {
      margin-top: 30px;

      li:first-child {
        width: 30%;

        img {
          width: 2.26rem;
          height: 2.4rem;
        }
      }

      li:last-child {
        width: 60%;
        font-size: 0.3rem;

        p:first-child {
          font-size: 0.3rem;
          font-weight: 600;
        }

        p:nth-child(2) {
          margin: 5px 0px;
          font-size: 0.26rem;
        }

        p:nth-child(3) {
          margin: 5px 0px;
          font-size: 0.26rem;
        }

        p {
          span {
            color: #ff0000;
          }
        }
      }

      .bookbtn {
        background: #14d1ed;
        color: #fff;
        width: 70%;
        margin: 0 auto;
        border-radius: 10px;
      }
    }
  }

  .download {
    background: #eee;
    // margin: 10px 0px;
    padding: .1rem .3rem 0;
    box-sizing: border-box;

    ul {
      padding: 10px 0px 20px 0px;

      li {
        font-size: 0.26rem;
        margin: 2px 0px;

        p:first-child {
          width: 79%;
          text-align: justify;
        }

        p:last-child {
          width: 20%;
          background: #feab3c;
          padding: 5px 0px;
          color: #fff;
          border-radius: 10px;
        }
      }
    }
  }

  .uldisplay {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
  }
  .newfoot{
    height: 1rem;
    line-height: 1rem;
    background: #ccc;
    p{
      color: #333;
      font-size: 0.2rem;
    }
  }
</style>



